import DaoAutocompleteDirective from './dao-autocomplete.directive.js';
import DaoAutocompleteTextDirective from './dao-autocomplete-text.directive.js';
const moduleName = 'dao.autocomplete';

angular.module(moduleName, [])
  .directive('daoAutocomplete', DaoAutocompleteDirective)
  .directive('daoAutocompleteText', DaoAutocompleteTextDirective)

export default moduleName;



