function PseudoDisableDirective() {
  'ngInject';

  return {
    restrict: 'A',
    priority: 599,
    replace: true,
    compile: (ele, attrs) => {
      const disableCondition = attrs.pseudoDisable;
      const clickEvent = attrs.ngClick ? attrs.ngClick : false;
      const clickEventText = `(${disableCondition}) ? false : (${clickEvent})`;

      // 解除元素的点击绑定事件
      ele.unbind('click');
      ele.off('click');

      // 再加上 click 事件 attr
      ele.attr('ng-click', clickEventText);
      attrs.ngClick = clickEventText;
    },
    template: (ele, attrs) => {
      const disableCondition = attrs.pseudoDisable;
      const pseudoDisableText = attrs.pseudoDisableText;
      const pseudoDisableTextEnable = attrs.pseudoDisableTextEnable;
      const tooltipEnableAddon = pseudoDisableTextEnable ? ` && (${pseudoDisableTextEnable})` : '';
      const ngClass = attrs.ngClass;
      const ngClassFirstBrace = (ngClass || '').indexOf('{');
      const ngClassLastBrace = (ngClass || '').lastIndexOf('}');

      // 给新元素添加 disable 的样式
      if (ngClass && ngClassFirstBrace !== -1 && ngClassLastBrace !== -1) {
        const ngClassOriginContent = ngClass.substring(ngClassFirstBrace + 1, ngClassLastBrace);
        ele.attr('ng-class', `{'disabled':${disableCondition}, ${ngClassOriginContent}}`);
        attrs.ngClass = `{'disabled':${disableCondition}, ${ngClassOriginContent}}`;
      } else {
        ele.attr('ng-class', `{'disabled':${disableCondition}}`);
      }
      ele.css('pointer-events', 'auto');

      // 给新元素加 tooltip
      ele.attr('dao-tooltip', `{{${pseudoDisableText}}}`);
      ele.attr('tooltip-append-to-body', true);
      ele.attr('tooltip-enable', `(${disableCondition})${tooltipEnableAddon}`);

      // 暂时去除元素的点击事件
      ele.removeAttr('ng-click');

      // 避免递归复制
      ele.removeAttr('pseudo-disable');

      return ele[0].outerHTML;
    },
  };
}

export default PseudoDisableDirective;



