import template from './dao-option.html';

export default function OptionDirective($compile) {
  'ngInject';

  function linkFun(scope, element, attrs, $ctrl) {
    const selectCtrl = $ctrl[0];
    const optionGroupCtrl = $ctrl[1];

    selectCtrl.addOption(scope);
    scope.$on('$destroy', () => {
      selectCtrl.deleteOption(scope);
      if (optionGroupCtrl) {
        optionGroupCtrl.deleteOption(scope);
      }
    });

    if (optionGroupCtrl) {
      optionGroupCtrl.addOption(scope);
    }

    function setSelected(value) {
      if (!!scope.disabled) {
        return;
      }
      const selected = value;
      selectCtrl.setSelected(selected);
    }
    scope.setSelected = setSelected;

    scope.isSelected = angular.equals(selectCtrl.ngModel, scope.value);

    scope.isShow = true;

    scope.$watch(() => scope.html, (value) => {
      if (!value) return;
      const el = angular.element(element[0].querySelector('.html'));
      el.html(value);
      $compile(el.contents())(scope);
    });
  }

  return {
    restrict: 'AE',
    replace: false,
    require: ['^^daoSelect', '?^daoOptionGroup'],
    scope: {
      value: '=',
      text: '=',
      html: '=',
      tabName: '@',
      disabled: '=',
    },
    template,
    link: linkFun,
  };
}



