import template from './editable-dao-input.html';
import './editable-dao-input.scss';

class editableDaoInputController {
  constructor($scope, $element, $timeout) {
    'ngInject';

    this.$scope = $scope;
    this.$el = $element;
    this.$timeout = $timeout;

    this._activate();
  }

  _activate() {
    // 数据备份
    this.dataOrigin = this.data;
    this.isEdit = this.options.editState;
    this.errorMessage = '';
  }

  // 更改
  edit() {
    if (this.options.editFun) {
      this.options.editFun();
    }
    this.isEdit = true;
    // 不在 disabled 状态时，才能设置焦点，所以要用到 $timeout
    this.$timeout(() => {
      angular.element(this.$el[0]).find('input')[0].focus();
    });
  }

  // ng-keyPress 不能监听 esc 键，所以用 keyUp，Firefox 在 keydown 下也不能监听 esc 键
  keyUp(e, data) {
    switch (e.keyCode) {
      case 13: // enter 键
        this.save(data);
        break;
      case 27: // esc 键
        this.cancel();
        break;
      default:
        break;
    }
  }

  // 保存
  save(data) {
    // 有检验函数
    if (this.options.checkFun) {
      const errorMessage = this.options.checkFun(data);
      // 检查不通过
      if (errorMessage) {
        this.errorMessage = errorMessage;
        this._focus();
        return;
      }
    }

    // 检查通过
    this.dataOrigin = this.data;
    this.isEdit = false;
    this.errorMessage = '';
    // 成功回调
    if (this.options.successFun) {
      this.options.successFun();
    }
  }

  // 取消
  cancel() {
    if (this.options.cancelFun) {
      this.options.cancelFun();
    }
    this.isEdit = false;
    this.errorMessage = '';
    this.data = this.dataOrigin;
  }

  // 焦点放到 input 上
  _focus() {
    angular.element(this.$el[0]).find('input')[0].focus();
  }
}

function EditableDaoInputDirective() {
  'ngInject';

  return {
    restrict: 'E',
    template,
    scope: {
      data: '=',
      options: '=',
    },
    replace: true,
    controller: editableDaoInputController,
    bindToController: true,
    controllerAs: 'vm',
  };
}

export default EditableDaoInputDirective;



