import template from './dao-switch.html';

function SwitchDirective() {
  function linkFun(scope, el, attrs) {
    scope.attrs = attrs;
    scope.isDisabled = false;
    if (attrs.disabled && attrs.disabled !== 'false') {
      scope.isDisabled = true;
    }

    scope.click = () => {
      scope.ngModel = !scope.ngModel;
      if (scope.ngChange) {
        scope.ngChange();
      }
    };
  }

  return {
    restrict: 'AE',
    replace: true,
    scope: {
      ngModel: '=',
      ngChange: '&',
      option: '=',
    },
    template,
    link: linkFun,
  };
}

export default SwitchDirective;



