import template from './dao-callout.html';

function DaoCalloutDirective() {
  'ngInject';

  return {
    restrict: 'E',
    scope: {
      canClose: '=',
      type: '@',
      title: '@',
    },
    template,
    replace: true,
    transclude: true,
  }
}

export default DaoCalloutDirective;



