function DaoAutocompleteTextDirective($compile) {
  'ngInject';

  class ctrl {
    constructor($scope, $element, $attrs, $timeout) {
      'ngInject';
      this.$element = $element;
      this.$scope = $scope;
      this.$timeout = $timeout;
      this.text = $element.html();
    }

    render() {
      this.originalText = this.originalText ? this.originalText : this.$element.html();
      const that = this;
      // $parent.dacvm.search触发的时候，li 元素还没有出现，所以要 timeout
      this.$timeout(() => {
        const searchText = that.keyword;
        // 替换原本文本
        const reg = new RegExp(`^${searchText}`, 'ig');
        const replacedText = that.originalText.match(reg);
        // debugger;
        that.$element.html(that.originalText.replace(reg, ''));
        // 删除上一次的强调文本
        const preEm = that.$element.parent().find('strong').remove();
        // 添加强调文本
        const emEle = angular.element('<strong>').addClass('dac-strong').text(replacedText);
        that.$element.parent()[0].insertBefore(emEle[0], that.$element[0]);
      }, 0);
    }
  }

  function link(scope, ele, attr, ctrl) {
    scope.$watch('$parent.dacvm.search', val => {
      ctrl.keyword = val;
      if (typeof(val) === 'string') {
        ctrl.render();
      }
    })
  }

  return {
    restrict: 'A',
    controller: ctrl,
    link,
    scope: false,
  };
}

export default DaoAutocompleteTextDirective;



