import template from './dao-autocomplete.html';
import DaoAutocompleteController from './dao-autocomplete.controller.js';
import './dao-autocomplete.scss';

function DaoAutocompleteDirective($compile) {
  'ngInject';

  function autocompleteLink(scope, ele, attr, ctrl, transclude) {
    transclude(clone => {
      let html = '';
      for (let i = 0; i < clone.length; i++) {
        // debugger
        if (clone[i].nodeType !== 1) continue;
        if (clone[i].nodeName === 'LI') {
          let ngRepeat = `(index, ${ctrl.itemName}) in dacvm.filteredOptions`;

          if (clone[i].getAttribute('category')) {
            ngRepeat = `${ngRepeat} | filter: ${clone[i].getAttribute('category')}`;
          }

          clone[i].setAttribute('ng-repeat', ngRepeat);
          clone[i].setAttribute('ng-mousedown', 'dacvm.onMousedown($event)');
          clone[i].setAttribute('ng-mouseover', 'dacvm.onMouseover(index)');
          clone[i].setAttribute('ng-mouseleave', 'dacvm.onMouseleave()');
          clone[i].setAttribute('class', 'dac-li');
          clone[i].setAttribute('ng-class', `{
            selected: dacvm.myGet(dacvm.filteredOptions[dacvm.index], dacvm.itemKey)
              === ${scope.dacvm.valueExpr}
          }`);
        }

        html += clone[i].outerHTML || '';
      }

      ele.find('ul')
        .attr('id', scope.dacvm.id)
        .append($compile(html)(scope));

      // 万一 options 变化，就需要重新编译li
      scope.$watchCollection(() => ctrl.getOptions(), val => {
        if (val) {
          ctrl.getOptions();
          ctrl.getFilteredOptions();

          ele.find('ul')
            .attr('id', scope.dacvm.id)
            .append($compile(html)(scope));
        }
      });

      // 监控外面传进来的onchange事件
      if (ctrl.ngChange) {
        scope.$watch('dacvm.ngModel', () => {
          if (typeof(ctrl.ngModel) === 'string') {
            ctrl.ngChange();
          }
        });
      }
    });
  }

  return {
    restrict: 'E',
    template,
    controller: DaoAutocompleteController,
    controllerAs: 'dacvm',
    bindToController: true,
    transclude: true,
    replace: true,
    scope: {
      ngModel: '=',
      placeholder: '=placeholder',
      optionsExpr: '@options',
      valueExpr: '@daoAutocompleteValue',
      textExpr: '@daoAutocompleteText',
      createPlaceholder: '=?createPlaceholder',
      emptyPlaceholder: '=?emptyPlaceholder',
      error: '=?error',
      errorMessage: '=?errorMessage',
      dropdownClass: '@?dropdownClass',
      selectLike: '=?selectLike',
      ngChange: '&?onChange',
      ngFocus: '&?onFocus',
      ngBlur: '&?onBlur',
      hotLoad: '<hotLoad',
    },
    link: {
      pre: autocompleteLink,
    },
  };
}

export default DaoAutocompleteDirective;



